<script>
  import { onMount } from "svelte";
  import Card from "./UI/Card.svelte";
  import Home from "./pages/Home.svelte";
  import CTET from "./pages/CTET.svelte";

  let currentPage;

  onMount(() => {
    currentPage = "Home";
    history.pushState("Home", "Home", "/Home");
  });
  const loadPage = e => {
    if (e.detail.page) currentPage = e.detail.page;
    else currentPage = e;
    history.pushState(currentPage, currentPage, currentPage);
  };

  window.onpopstate = e => {
    currentPage = e.state;
  };
</script>

<style>
  .main {
    margin: 2.5rem 0 2rem;
    padding: 0.5rem 1rem;
  }
</style>

<div class="main">
  {#if currentPage === 'Home'}
    <Home on:loadpage={loadPage} />
  {:else if currentPage === 'CTET'}
    <CTET />
  {/if}
</div>
